.Search {
    display: flex;
    max-width: 400px;
    flex-direction: column;
    justify-content: center;
    text-align: center;
}
.Search-title {
    font-size: 1.5rem;
    font-weight: 500;
    margin-bottom: 15px;
    text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.3);
}
.Search-select {
    font-size: 1rem;
    margin: 16px 0;
    border-radius: 8px;
    padding: 8px;
    border: none;
}
.Search-continue {
    background-color: white;
    color: #293684;
    font-size: .8rem;
    font-weight: bold;
    padding: 6px 12px;
    margin: 0 160px;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    box-shadow: 2px 4px 10px rgba(0, 0, 0, 0.2);
    transition: all 0.3s ease;
}
.Search-continue:hover {
    background-color: #dadee9;
}
